

.contenido-about{

    @include tablet{
       @include grid(2, 1);
    }
    
}

 .titulo-about{
    margin: 2rem 0 2rem 1rem;
    max-width: 95%;


    h1{
        font-size: 4rem;
         @include tablet{
             font-size: 5.5rem;
        }
    }
}

@include desktop{
    .titulo-about{
        width: 60rem;
        height: 30rem;

        @include desktop{
            margin-left: 5rem;
        }


        h1{
            font-size: 7rem;
        }
    }
}

.carta-about{
    display: flex;
    flex-direction: column;
    padding: 1rem;
    
    .vector{
        display: flex;
        flex-direction: column;
        text-align: center;
        
        
        .imagen-vector{
            margin: 0 auto;
            width: 4rem;
            height: 4rem;
            margin: 1rem auto 0;
            
        }

        h3{
            margin: auto;
            font-size: 2.5rem;
            font-weight: $delgada;
            margin: 1rem auto;
        }
        
    }


    p{
        font-size: 1.8rem;
        visibility: visible;
        
        .ocultar-parrafo{
            visibility: hidden;
        }  
    }

}

.parrafo-principal{
    font-size: 2.2rem;

    span{
        font-size: 2.5rem;
    }
}

.imagen-personal{
    width:60rem ;
    height:40rem ;
    margin: 2rem 0 2rem auto;
    background-color: $blanco;
}

@include desktop{
    .certificados{
        @include grid(2,5)
    }
}

.imagen-certificado{
    width:30rem ;
    height:20rem ;
    margin: 2rem 0 2rem auto;
    background-color: $blanco;
    margin:1rem auto;
}

