      .hola.ocultar::after {
        content: 'Abuot'; }
  .soy {
    font-size: 12rem; }
    .soy.mostrar::after {
      content: 'I am'; }
    .soy.ocultar {
      margin-left: 3rem; }
      .soy.ocultar::after {
        content: 'Work'; }
  .nombre {
    font-size: 12rem; }
