
//margenes y alineaciones
.no-margin{
    margin: 0;
}

.centrar-izq-der{
    margin:0 auto;
}

.centrar-arriba-abajo{
    margin: auto 0;
}

.alinear-derecha{
    justify-content: flex-end;
}

.alinear-izquieda{
    justify-content: flex-start;
}



//colores , tama√±os
.bg-color-principal {
    background-color: $negro;
}

.bg-color-secundario {
    background-color:$negro;
}

.color-azul{
    color: $azul;
}

.color-blanco{
    color: $blanco;
}
.invertir{
    filter: invert(100%);
}

.delgada{
    font-weight: $delgada;
}

.bold{
    font-weight: $bold;
}
.normal{
    font-weight: $normal;
}