+html{
    font-size:62.5%;
    box-sizing: border-box;
    
}
*,*::before,*::after/*ayuda a mantener el pading y bordes dentro de la medida*/{
    box-sizing: inherit;
}

h1,h2,h3,h4,a{
    font-family: $fuentePrincipal;
}
h1{
    font-size: 5rem;
}
h2{
    font-size: 4rem;
}
h3{
    font-size: 3rem;
}
h4{
    font-size:2.5rem;
}
a{
    text-decoration: none;
    font-size: 2rem;
}
p{
    font-family: $fuenteSecundaria;
    font-size: 2rem;
}
img{
    max-width: 100%;
}

.contenedor{
    width: 100rem;
    margin:0 auto ;
    max-width: 95%;
}

.contenedor-peque√±o{
    width: 58rem;
    margin:0 auto ;
    max-width: 95%;
}

input:not([type='button']){
    padding:.5rem ;
    background-color:$grisOscuro;
    color: $blanco;
    border:none;
    font-family: $fuenteSecundaria;
    font-size: 1.5rem;
    margin:.5rem;
    border-radius: .5rem;
}
