.barra{
    margin: 1rem 1rem 1rem;
    display: flex;
    justify-content: space-between;
    align-items:center ;
    @include max-tablet{
        height: 15rem; 
    }
       
    .logotipo{
        max-width: 5rem;
        filter: invert(100%); 
        
        @include desktop{
            &.fijo{
                width: 100%;
                position: fixed;
                right: 0;
                display: flex;
                margin:3rem 1rem 0 auto;  
            }
        }  
    } 
}

.navegacion{
    a{
        font-family: $fuenteSecundaria;
    }
    visibility: visible;
    opacity: 0;
    display: flex;
    flex-direction: column;
    position: absolute;
    transition: visibility .1s linear, opacity .1s linear, height 5s ;
    
    &.mostrar-nav{
        visibility: visible;
        opacity: 1;
        transition: visibility 1s ease-in-out, opacity .1s linear, ;
    }
     
}



@include tablet{
    .navegacion{
        flex-direction: row;
        visibility: visible;
        opacity: 1;
        
        a{
            margin-right:1rem ;
            &:last-of-type{
                margin: 0;
            }
        }
    }
}




@include max-tablet{
    .menu{
        margin-top: -3rem;
        visibility: visible;
        opacity: 1;
        transition: visibility 1s ease-in-out, opacity .1s linear, ;
    }
}


