
.portada-escritorio{
    margin:9rem auto;
    width: 80rem;
    max-width: 95%;

    @include max-tablet{
        visibility: visible;
        opacity: 1;

    }
}


@include desktop{
    .hola{
        font-size: 12rem;       
        &.mostrar::after{
            content:'Hello'; 
        }    
        
        &.ocultar{
            margin-left:3rem ;
            &::after{
            content: 'Abuot';
            }
        }
    }        
    
    .soy{
        font-size: 12rem;       
        &.mostrar::after{
            content:'I am'; 
        }    
        
        
        &.ocultar{
            margin-left:3rem ;
            &::after{
            content: 'Work';
            }
        }
        
            
    }
    .nombre{
        font-size: 12rem;       
        &.mostrar::after{
            content:'Cristopher'; 
        }    
        
        &.ocultar{
            margin-left:3rem ;
            &::after{
            content: 'Contact';
            }
        }
    }
}




