@mixin grid ($columnas,$separacion){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $separacion;
}

@mixin telefono {
    @media (min-width: #{$celular}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tableta}) {
        @content;
    }
}

@mixin max-tablet {
    @media (max-width: #{767px}) {
        @content;
    }
}

  
@mixin desktop {
    @media (min-width: #{$escritorio}) {
        @content;
    }
}

@mixin max-desktop {
    @media (max-width: #{$escritorio}) {
        @content;
    }
}