
.menu{
    visibility: hidden;
    opacity: 0;
    height: 3rem;
    width: 3rem;
    // border: 1px solid $blanco;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    transition: all .5s ease-in-out;

}
.boton-menu{
    width: 2.5rem;
    height: .3rem;
    background-color: $blanco;
    border-radius: .5rem;
    transition: all .5s ease-in-out;

}

.boton-menu::before,
.boton-menu::after{
    content: '';
    position: absolute;
    width: 2.5rem;
    height: .3rem;
    background-color: $blanco;
    border-radius: .5rem;
    transition: all .5s ease-in-out;
}

.boton-menu::before{
    transform:translateY(-.8rem) ;
}
.boton-menu::after{
    transform:translateY(.8rem) ;
}

//animacion

.menu.open
.boton-menu{
    transform: translateX(-.5rem);
    background: transparent;
    box-shadow: none;

}


.menu.open 
.boton-menu::before{
    transform:rotate(45deg) translate(35px,-35px) ;
}
.menu.open 
.boton-menu::after{
    transform:rotate(-45deg) translate(35px, 35px); 
}