.portada{
    
    height: 60rem;
    &:hover{
        cursor:pointer;
    }
}
    



.hola-responsive,
.soy-responsive,
.nombre-responsive{
    line-height: 1.4;
    font-size: 4rem;
    @include telefono{
        font-size: 6rem;
    }

    @include tablet{
        font-size: 8rem;  
    }  
}

@include max-desktop{
    .hola-responsive{
        &::after{
            content: 'Hello';
            visibility: visible;
        }
    }
    
    .soy-responsive{
        &::after{
            content: 'I am';
            visibility: visible;
        }
    }
    
    .nombre-responsive{
        &::after{
            content: 'Cristopher';
            visibility: visible;
        }
    }
}
