.header-contacto{
    margin: 2rem 0 2rem 1rem;
    max-width: 95%;
    
    h1{
        font-size: 3rem;
        @include tablet{
            font-size: 4.5rem;
        }
    }
    
}

.formulario{
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    .name{
        display: flex;
        flex-direction: column;
        @include tablet{
            input{
                width: 100%;
            }
        }
        
    
        @include tablet{
        flex-direction: row;
        
        }
        
    } 
    
}

.boton{
    margin: 1rem 1rem 1rem auto;
    
    border:none;
    border-radius: .3rem;
    padding:.5rem 2rem ;
    max-width: 8rem; 
}

.links{
    margin-top:2rem;
    @include tablet{
        @include grid(3, 2);
    } 
    .links-contacto{
        text-align: center;
        img{
            max-width: 5rem;
        }
    } 
    
}



